@import url(https://fonts.googleapis.com/css2?family=Chelsea+Market&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Chelsea Market', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.searchBar {
    text-align: center;
    margin-top: 8%;
}
form {
    box-sizing: border-box;
}
#searchInput {
    width: 500px;
    max-width: 90%;
    height: 35px;
    padding-left: 10px;
    border-radius: 30px;
    border-color: rgb(0, 80, 184);
    font-size: 20px;
    transition: 1s;
}
#searchInput:focus {
outline: none;
box-shadow: 1px 2px 10px 7px #54d6fd ;
}
#searchButton {
    height: 41px;
    width: 80px;
    margin-left: -80px;
    border-radius: 0 30px 30px 0;
    border-color: rgb(0, 80, 184);
    outline: none;
    color: white;
    background-color: rgb(37, 128, 247);
    font-size: 20px;
}
#searchButton:hover {
    background-color: rgb(0, 80, 184);
    cursor: pointer;
}
.results {
    text-align: center;
    color: rgb(10, 106, 196);
}
.location {
width: 100%;
}
#location{
    font-size: 40px;
}
.weather-info {
    width: 600px;
    max-width: 100%;
    margin: auto;
    display: grid;
    grid-template-columns: auto auto;
}
.temp {
    font-size: 80px;
}
.description {
    font-size: 30px;
}

@media (min-width: 900px) {
    .results {
        width: 50%;
        margin: auto;
    }
    .temp {
        font-size: 150px;
    }
    #location {
        font-size: 50px;
    }
}
.outer {
    box-sizing: border-box;
    width: 100%;
    margin: 10px 0 10px 0;
    padding: 10px 0 20px 0;
    height: 200px;
    background-color: rgba(71, 253, 253, 0.24);
    border-radius: 10px;
    float: left;
    color: rgb(10, 106, 196);
    box-shadow: 5px 10px 10px 10px #888888;
}
.outer p {
   font-size: 18px;
   font-weight: 500;
}
.outer p.temp {
    font-size: 25px;
}

@media (min-width: 800px) {
    .outer {
        width:15%;
        margin: 1% 2.5% 0 2.5%;
    }
}

@media (min-width: 500px) and (max-width: 799px) {
    .outer {
        width: 45%;
        margin:2.5%
    }
}
.header {
    font-size: 20px;
    color: rgb(10, 106, 196);
    text-align: left;
    margin: 100px 0 10px 2.5%;
}
.container {
  text-align: center;
  background-image:url(/Weather-App/static/media/bckgrd.99840be3.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 100%;
}

